export default {
  data () {
    return {
      tabArr: [
        {
          id: 'home',
          name: '首页',
          icon_id: 'icon-home'
        },
        {
          id: 'attendance',
          name: '考勤',
          icon_id: 'icon-sala'
        },
        {
          id: 'leave',
          name: '请假',
          icon_id: 'icon-server'
        },
        {
          id: 'mine',
          name: '我的',
          icon_id: 'icon-account'
        }
      ],
      fixed: true,
      selected: '',
      transitionName: ''
    }
  },
  created () {
    this.selected = this.$route.name
  },
  watch: {
    $route (to, from) {
      this.selected = to.name
      let transitionArr = this.tabArr.map((item) => {
        return item.id
      })
      var toIndex = transitionArr.indexOf(to.name)
      var fromIndex = transitionArr.indexOf(from.name)
      if (toIndex > -1 && fromIndex > -1) {
        if (toIndex > fromIndex) {
          this.transitionName = 'slide-right'
        } else {
          this.transitionName = 'slide-left'
        }
      } else {
        this.transitionName = this.$store.getters.getTransitionName
      }
    },
    selected (val) {
      this.$router.push({ name: val })
    }

  }
}



